@import url('https://fonts.googleapis.com/css2?family=Kalam&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500&display=swap');

.app{
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main{
  width: 72vw;
  height: 42vw;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  color: #000000;
  font-family: "Kalam", cursive;
  font-size: 4vw;
}

p{
  color: #06130f;
  font-family: "Kalam", cursive;
  font-size: 3vw;
}
p em{
  font-weight: bold;
  color: #FFF1FA;
}

.boardTitle{
  color: #000000;
  font-family: "Kalam", cursive;
  font-size: 4vw;

  width: 30vw;
  height: 4vw;
  
  background: #99714a;

  border-style: solid solid none solid;
  border-color:#99714a;
  border-width: 1.5vw;
  border-radius: 1vw 1vw 0vw 0vw;

  line-height: 4vw;
  text-align: center;
}
.board{
  display: grid;
  grid-template-columns: repeat(4, 16vw);
  grid-auto-rows: 20vw;
  gap: 1vw;
  background-color: #C4A484;
  padding: 3vw;

  border-style: solid;
  border-color:#99714a;
  border-width: 1.5vw;
  border-radius: 1vw;
}
img{
  width: 100%;
  height: 100%;
}


img:nth-child(2n) {transform: rotate(-2deg); }
img:nth-child(3n) {transform: rotate(6deg); }
img:nth-child(4n) {transform: rotate(-8deg); }
img:nth-child(1n) {transform: rotate(8deg); }
img:nth-child(5n) {transform: rotate(-6deg); }
img:nth-child(6n) {transform: rotate(10deg); }
img:nth-child(7n) {transform: rotate(8deg); }
img:nth-child(8n) {transform: rotate(-8deg); }
.body{
  border: none;
  grid-column: span 2;
  display: grid;
  grid-template-columns: repeat(3, calc(32vw/3));
  grid-template-rows: 5vw 14vw;
  gap: 0.5vw;
}
.timeTitle{
  grid-column: span 3;

  color: #06130f;
  font-family: "Kalam", cursive;
  font-size: 3vw;

  text-align: center;
}
.timeUnit{
  background: white;

  border-style: solid;
  border-color: white;
  border-width: 0.5vw;
  border-radius: 0.4vw;

  text-align: center;
  vertical-align: middle;

  font-family: "Kalam", cursive;
  line-height: 4vw;
  font-size: 1.8vw;
}
.timeNum{
  border: none;
  background: lightgray;
  width: calc(29vw/3);
  height: calc(29vw/3);
  line-height: calc(29vw/3);

  border-color: lightgray;
  border-radius: 0.2vw;
  
  font-size: 5vw;
  font-family: 'Quicksand', sans-serif;
  font-weight: 500;
}